@import url(https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;0,600;0,700;0,800;1,300;1,400;1,600;1,700;1,800&display=swap);
.class {
  display: flex;
  flex-direction: column;
  width: 100%;
  align-items: center;
  justify-content: center;
}
.class button {
  width: 98%;
}
.class__buttons {
  margin-top: 20px;
  width: 400px;
  height: 400px;
  overflow-y: scroll;
  border: 1px solid #aaa;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.class .class__buttons h2 {
  color: #fff;
  width: 60%;
  border-bottom: 1px solid #444;
  padding: 10px;
}
@media only screen and (max-width: 600px) {
  .class__buttons {
    width: 90%;
  }
}

.teacher {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.teacher ul {
  width: 400px;
  height: 400px;
  overflow-y: scroll;
  border: 1px solid white;
  display: flex;
  flex-direction: column;
  text-align: center;
  align-items: center;
  padding: 0;
}
.teacher ul h2 {
  padding: 10px;
  width: 60%;
  border-bottom: 1px solid #444;
}
.teacher__form {
  margin: 20px;
}
@media only screen and (max-width: 590px) {
  .form__inputs {
    display: flex;
    flex-direction: column;
  }
  .teacher ul {
    width: 90%;
  }
  .teacher ul li {
    width: 30%;
  }
}

.student {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.student .buttons {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  width: 50%;
  border: 1px solid #aaa;
  width: 400px;
  height: 400px;
  overflow-y: scroll;
}

.api {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.api__table {
  display: flex;
  flex-direction: column;
  margin-top: 20px;
  border: 1px solid #aaa;
  padding: 10px 30px;
  height: 400px;
  overflow-y: scroll;
  width: 100%;
}
.api__table p {
  margin: 20px 30px;
}
.api pre {
  border: 1px solid #aaa;
  margin-top: 20px;
  padding: 40px;
  width: 400px;
  height: 400px;
  overflow-y: scroll;
}
.table__row {
  display: flex;
  border-bottom: 1px solid #aaa;
  align-items: center;
}
.row__item {
  display: flex;
  align-items: center;
}
@media only screen and (max-width: 1034px) {
  .table__row,
  .row__item {
    flex-direction: column;
  }
}
@media only screen and (max-width: 461px) {
  .api pre {
    width: 300px;
  }
  .api__buttons {
    width: 100%;
    display: flex;
    flex-direction: column;
  }
}

* {
  font-family: "Open Sans", sans-serif;
  font-size: 20px;
  color: #fff;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background: black;
}
body::after {
  background: url(/sincoabr-school/static/media/banner.0e7746d5.jpg);
  content: "";
  opacity: 0.4;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
}
input {
  padding: 12px;
  outline: none;
  background: transparent;
  border: none;
  border-bottom: 1px solid #aaa;
  margin: 10px;
}
.buttons {
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
}
Button {
  padding: 15px 45px;
  margin: 3px;
  cursor: pointer;
  border: 1px solid #aaa;
  transition: all 0.3s ease-in;
  outline: none;
  background: rgba(100, 100, 100, 0.1);
}
Button:hover {
  background: rgba(100, 100, 100, 0.5);
  border-color: #fff;
}
@media only screen and (max-width: 800px) {
  body {
    height: 100%;
    padding: 50px 0;
  }
  .buttons {
    flex-direction: column;
  }
  Button {
    padding: 10px 35px;
  }
  .teacher ul {
    width: 90%;
  }
  .teacher ul li {
    width: 30%;
  }
}

